<template>
    <div class="row mb-3">
        <div class="col">
            <h5> {{ id }}</h5>
            <Line :chart-options="chartOptions" :chart-data="chartData.value" />
        </div>
    </div>
</template>

<script setup>
import { computed, reactive } from "vue";
import { useVSUIStore } from '@/stores/vsui_store.js'
import { Line } from 'vue-chartjs';
import {
    Chart as ChartJS,
    Title,
    Tooltip,
    Legend,
    LineElement,
    LinearScale,
    PointElement,
    CategoryScale,
} from 'chart.js'

ChartJS.register(
    Title,
    Tooltip,
    Legend,
    LineElement,
    LinearScale,
    PointElement,
    CategoryScale
)

const store = useVSUIStore();
const socket = store.socket;

const props = defineProps({
    id: String
})

const my_data = reactive({value: {'x':[],'y':[]}})

const chartData = computed(() => {
    return { value: {
    labels: my_data.value.x,
    datasets: [
        {
            label: "test",
            backgroundColor: '#f87979',
            data: my_data.value.y
        }
    ]
}}})

const chartOptions = {
    responsive: true,
    maintainAspectRatio: false
}

socket.on('display_update', (data) => {
  let new_data = data.data
  if (props.id in new_data) {
    my_data.value = new_data[props.id]
  }
})
</script>

