<template>
    <div class="card mb-2">
        <div class="card-header">
                <div class="float-start">
                    {{ id }}
                </div>
                <div class="float-end">
                    <h3><i class="bi bi-gear-fill">  </i><i class="bi bi-play-fill"></i></h3>
                </div>
        </div>
        <div class="card-body">
            <component v-for="element in struct.value" :key="element.key" :is="components[element.component]" :id="element.key" />
        </div>
    </div>
</template>

<script setup>
// Child Components - Elements
import { reactive } from 'vue';
import { useVSUIStore } from '@/stores/vsui_store.js'
import ProgressElement from './ProgressElement.vue';
import LogElement from './LogElement.vue';
import ChartElement from './ChartElement.vue';
import GalleryElement from './GalleryElement.vue';
const store = useVSUIStore();
const socket = store.socket;

const components = {
    'ProgressElement' : ProgressElement,
    'LogElement' : LogElement,
    'ChartElement' : ChartElement,
    'GalleryElement' : GalleryElement
}

// eslint-disable-next-line
const props = defineProps({
    id: { required: true, type: String },
    active: { default: false, type: Boolean }
})

// "struct":
const struct = reactive({value: [{ "key": "Nothing to Show", "component": "LogElement" }]})

socket.on('display_update', (data) => {
  let new_data = data.data
  if (props.id in new_data) {
    struct.value = new_data[props.id]
  }
})
</script>

